import React, {useState} from 'react'

export default function TextForm(props) {
    const [text, setText] = useState('');

    const handleUpClick = () => {
        setText(text.toUpperCase())
        props.showAlert("Converted to Upper case", "success")
    }

    function titleCase(str) {
        return str.toLowerCase().replace(/\b\w/g, s => s.toUpperCase());
      }

    const handleLowClick = () => {
        setText(text.toLowerCase())
        props.showAlert("Converted to Lower case", "success")
    }

    const handleClearClick = () => {
        setText("")
        props.showAlert("Text Cleared!", "success")
        
    }

    const handleTitleClick = () => {
        setText(titleCase(text))
        props.showAlert("Converted to Title case", "success")
    }

    const handleOnChange = (e) => {
        console.log(e.target.value)
        setText(e.target.value)
    }
    return (
        <div>
            <div className='container' style={{color: props.mode === "dark"? 'white': 'black'}}>
                    <h1>{props.heading}</h1>
                    <textarea className="form-control" style={{backgroundColor: props.mode === "dark"? 'grey': 'white', color: props.mode === "dark"? 'white': 'black'}} id="myBox" rows="8" value={text} onChange={handleOnChange}></textarea>
                    <button className='btn btn-primary my-4' onClick={handleUpClick}>Convert to UpperCase</button>
                    <button className='btn btn-primary my-4 mx-2' onClick={handleLowClick}>Convert to Lowercase</button>
                    <button className='btn btn-primary my-4 mx-2' onClick={handleTitleClick}>Convert to TitleCase</button>
                    <button className='btn btn-primary my-4 mx-2' onClick={handleClearClick}>Clear Text</button>
            </div>
            <div style={{color: props.mode === "dark"? 'white': 'black'}}>
                <h2>Your Text Summary</h2>
                <p>{text.split(" ").length} words and {text.length} characters</p>
                <p>{0.008 * text.split(" ").length} Minutes Read</p>
                <h2>Preview</h2>
                <p>{text}</p>
            </div>
        </div>
    )
}



